









































.router-view {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.router-view-transition-enter,
.router-view-transition-leave {
  opacity: 0;
  -webkit-transform: translate3d(0, 1rem, 0);
  transform: translate3d(0, 1rem, 0);
}



